.Number_container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
    /* background-color: white; */
}

.logo_container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo{
    width: 140px;
    height: 140px;
}
@media (max-width: 480px) {
    .logo{
        width: 70px;
        height: 70px;
    }
}
.contacts_container {
    position: relative;
    padding: 10px;
    border-radius: 15px;
    overflow: hidden; /* Ensures border animation stays within bounds */
}


.contacts_container-list{
    display: flex;
    flex-direction: column;
}
.contacts_list-item{
    font-size: 20px;
    margin-right: 5px;
    font-weight: 700;
}
.contacts_list-item_first{
    margin-right: 5px;
    color: var(--color-green-elements);
}
.contacts_list-link {
    color: var(--color-black-elements);
    transition: 0.5s;
}
.contacts_list-link:hover{
color: var(--color-green-elements);
}