.feedback-border {
  position: relative;
  display: inline-block;
  border-radius: 5px;
  padding: 6px;
  overflow: hidden;
}

.feedback-border::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 5px;
  padding: 2px;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: exclude;
  background: linear-gradient(to right, var(--color-green-elements), var(--color-salat-elements));
  border-radius: 12.21px;
}

.feedback {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 258.96px;
  height: 66.66px;
  background: linear-gradient(to right, var(--color-green-elements), var(--color-salat-elements));
  border-radius: 12.21px;
  animation: scaleAnimation 4s infinite; 
  transition: scale 0.5s ease; 
}

.feedback-border:hover .feedback {
  animation-play-state: paused;
  scale: 0.8; 
}

@keyframes scaleAnimation {
  0% {
      transform: scale(1);
  }
  50% {
      transform: scale(0.8);
  }
  100% {
      transform: scale(1);
  }
}





