.footer{
    position: relative;
    background-color: #ffffff; /* Серый фон */
    width: 100%;
    overflow: hidden;
}

.footer-container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 110px;

}
.footer-contacts{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 0px 130px;

}
.footer-contacts_title{
    color: #ffffff;
}

.footer-contacts_container-list{
    white-space: nowrap;
}
.footer-contacts_list-link{
    color: #ffffff;
    transition: 0.5s;
}
.footer-contacts_list-link:hover{
    color: var(--color-salat-elements);
}

.footer-contacts_list-item{
    font-size: 20px;
    font-weight: 700;
}
.footer-contacts_list-item_first{
    color: var(--color-salat-elements);
}
.footer-contacts_map{
    display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
background-color: #636363;
border-radius: 15px;
}
@media (max-width:480px) {

    .footer-contacts_map{flex-direction: column;}
}

.footer_bot-info{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    background-color: #636363;
    color: #ffffff;
}
.social_network-list{
    display: flex;
    gap: 20px;
}
.social_network-item{
padding: 20px;
}


.social_network_svg{
    width: 36px;
    fill: #ffffff;
    cursor: pointer;
    transition: 0.5s;
}
.social_network_svg:hover{
    fill: var(--color-salat-elements)
}
@media (max-width:480px) {
    .map-container{
    border-radius: 15px;
    margin-bottom: 10px;
    }

}
