


.main {
    background-color: #dedede;
    width: 100%;
    overflow: hidden;
    position: relative;
}
.main_container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin-bottom: 150px;
}


.mrg-top {
    margin-top: 150px;
    width: 80%;
    box-shadow: rgb(153, 153, 153) 0px 0px 5px 3px;
    border-radius: 15px;
    background: white;
}



.roof-title{
    padding: 30px 0 30px 0;
    text-align: center;
    color: var(--color-green-elements);
}

.special-text_list li::before {
    content: "✔";
    position: absolute;
    left: 0;
    font-size: 16px;
    color: var(--color-green-elements);
  }
  .special-text_list li {
    font-size: 18px;
    line-height: 1.6;
    margin: 8px 0;
    padding-left: 25px;
    position: relative;
    color: #555;
  }

.roof_special-text{
width: 90%;
}

.roof_bot-div{
    background: linear-gradient(145deg, #e6e6e6, #f3f3f3);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
}
.roof_bot-div h3 {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 10px;
    border-bottom: 2px solid var(--color-green-elements);
    display: inline-block;
    padding-bottom: 5px;
  }

  .roof_bot-div {
    background: linear-gradient(145deg, #f3f3f3, #e6e6e6);
    border-radius: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px 0;
    width: 90%;
  }

  .roof_bot-div:hover {
    background: linear-gradient(145deg, #e6e6e6, #f3f3f3);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
  }
