}

.special-text_item{

}

.roof_special-container{
  display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 0 0 30px;
    z-index: 2; /* Отправляем картинку на задний план */
}

.svg-materials {
  width: 13%;
  position: absolute;
  opacity: 0; /* Элементы изначально невидимы */
  transform: translateY(20px); /* Добавляем начальное смещение */
  transition: opacity 0.5s ease, transform 0.5s ease; /* Плавный переход */
}

.svg-materials1 {
  top: 16vh;
  right: 1vw;
  transform: translateY(20px); /* Начальное смещение */
}

.svg-materials2 {
  top: 36vh;
  right: 1vw;
  transform: translateY(20px) rotate(181deg); /* Начальное смещение и поворот */
}

.svg-materials3 {
  top: 53vh;
  right: 1vw;
  transform: translateY(20px); /* Начальное смещение */
}

.svg-materials4 {
  top: 76vh;
  right: 1vw;
  transform: translateY(20px); /* Начальное смещение */
}

.svg-materials5 {
  top: 16vh;
  left: 1vw;
  transform: translateY(20px) rotate(266deg); /* Начальное смещение и поворот */
}

.svg-materials6 {
  top: 36vh;
  left: 1vw;
  transform: translateY(20px) rotate(266deg); /* Начальное смещение и поворот */
}

.svg-materials7 {
  top: 53vh;
  left: 1vw;
  transform: translateY(20px) rotate(266deg); /* Начальное смещение и поворот */
}

.svg-materials8 {
  top: 76vh;
  left: 1vw;
  transform: translateY(20px) rotate(295deg); /* Начальное смещение и поворот */
}

/* Эффект при наведении */
.main_container:hover .svg-materials1 {
  opacity: 1;
  transform: translateY(0); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials2 {
  opacity: 1;
  transform: translateY(0) rotate(181deg); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials3 {
  opacity: 1;
  transform: translateY(0); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials4 {
  opacity: 1;
  transform: translateY(0); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials5 {
  opacity: 1;
  transform: translateY(0) rotate(266deg); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials6 {
  opacity: 1;
  transform: translateY(0) rotate(266deg); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials7 {
  opacity: 1;
  transform: translateY(0) rotate(266deg); /* Возвращаемся в исходное положение */
}

.main_container:hover .svg-materials8 {
  opacity: 1;
  transform: translateY(0) rotate(295deg); /* Возвращаемся в исходное положение */
}


