@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Анимация выезда справа */
@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Применение анимации */
.slide-in-left {
  animation: slideInFromLeft 1s ease-out forwards;
  display: flex
  ;
      align-items: center;
      justify-content: center;
}
.loading-fallback {
